<template>
  <div class="py-2" v-if="list.length">
      <div class="p-2 pt-0 fw-bold">{{set.title}}</div>
      <ViewList :set="set.set" :data="list"/>
  </div>
</template>

<script>
import ViewList from '../block/start';
import axios from 'axios'
export default {
  components: { 
    ViewList,
  },
  props: {
    set:{},
    data:{},
  },
  data(){
    return {
        list:[],
    }
  },
  mounted(){
      this.getlist();  
  },
  methods:{
      getlist(){
        axios({method: 'post',url: this.$store.state.apiset.url+'/driver/list',params:{token:this.$store.state.token},data:this.form,}).then(response => {
        this.loading=false;
        let res=response.data;
        if(res.code==1){
            if(res.data){
            this.list=res.data;
            }
        }else{
        this.$toast({type:'result',data:res});
        }
        }).catch(function (error) {this.loading=false;console.log(error)})
      }
  }
}
</script>

<style>

</style>


