<template>
  <div class="bg-white">
      <div class="d-flex justify-content-between p-2">
        <div class="bd-highlight fw-bold">{{set.title}}</div>
        <div class="bd-highlight text-black-50 fs-normal" @click="$tourl(set.url)">{{set.rightname}} 
          <span v-if="set.number"><span class="text-primary" >{{info.number}}</span> {{set.unit}}</span><span class="bi bi-chevron-right"></span></div>
      </div>
      <div class="py-2">
        <ViewList :set="set.set" :data="info.list"/>
      </div>
  </div>
</template>

<script>
import ViewList from '../block/start';
import axios from 'axios'
export default {
  components: { 
    ViewList,
  },
  props: {
    set:{},
    data:{},
  },
  data(){
    return {
        info:{
          list:[],
        },
    }
  },
  mounted(){
      this.getlist();  
  },
  methods:{
      getlist(){
        axios({method: 'post',url: this.$store.state.apiset.url+'/driver/cityinner',params:{token:this.$store.state.token},data:this.form,}).then(response => {
        this.loading=false;
        let res=response.data;
        if(res.code==1){
            if(res.data){
            this.info=res.data;
            }
        }else{
        this.$toast({type:'result',data:res});
        }
        }).catch(function (error) {this.loading=false;console.log(error)})
      }
  }
}
</script>

<style>

</style>


